<template>
  <div class="login">
    <div class="user-login">
      <img src="@/assets/images/组-3_03.png">
      <img class="logo" src="@/assets/images/分组 2.png">
      <el-form
        :model="loginForm"
        :rules="loginrules"
        label-position="left"
        label-width="0px"
        class="login-form"
      >
        <el-form-item prop="username">
          <el-input type="text" v-model="loginForm.username" prefix-icon="el-icon-user" placeholder="用户名"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            class="password"
            type="password"
            v-model="loginForm.password"
            placeholder="密码"
            prefix-icon="el-icon-lock"
            @keyup.enter.native="handleLogin()"
          ></el-input>
        </el-form-item>
        <el-button
          type="primary"
          class="login-button"
          @click="handleLogin()"
        >
          登录
        </el-button>
      </el-form>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'Login',
  data() {
    return {
      logining: false,
      loginForm: {
        username: '',
        password: ''
      },
      loginform:'',
      loginrules: {
        username: [{ required: true, message: '请输入账号', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    handleLogin(){
      this.$axios({
        url: '/api/users/login',
        method: 'post',
        data: {
          'username': this.loginForm.username,
          'password': this.loginForm.password
        }
      }).then((res) =>{
        if(res.data.result){
          const data = res.data.data
          window.localStorage['token'] = data.token
          const userinfo = data.userInfo
          // console.log(userinfo)
          window.localStorage['uid'] = userinfo.username
          // console.log(localStorage.getItem('uid'))
          window.localStorage['name'] = userinfo.name
          userinfo.roles.forEach(item => {
            window.localStorage['role'] = item.rid
            if(localStorage.getItem('token')){
              if(localStorage.getItem('role') == 1){
                this.$router.push('/adminmain')
              }
              else if(localStorage.getItem('role')== 2){
                this.$router.push('/usermain')
              }
              else if(localStorage.getItem('role') == 3){
                this.$router.push('/adminmain')
              }
            }else{
              this.$router.push('/')
              alert('res.msg')
            }
          })
        }else{
          alert('用户名或密码错误，请重新输入！')
        }
      })
    }
  }
}
</script>
<style lang="stylus" scoped>
  .login
    position: absolute
    top: 0
    left: 0
    right: 0
    bottom: 0
    background: #1D253C
    .user-login
      position: absolute
      top: 63px
      left: 205px
      width: 927px
      height: 524px
      background: #FFFFFF
      box-shadow: 0 0 40px 0 rgba(29,25,44,0.54)
      .logo
        position: absolute
        top: 66px
        left: 523px
      .login-form
        position: absolute
        left: 532px
        top: 160px
        width: 288px
        .password
          margin-top: 20px
        .login-button
          position: absolute
          left: 1px
          top: 165px
          width: 125px
          height: 44px
          line-height: 20px
          font-size: 16px
          color: #ffffff
          text-align: center
          letter-spacing: 0
          border-radius: 30.5px
          background-image: linear-gradient(90deg, #59E3FF 0%, #1F9CFF 100%)
</style>



